import api from "@/api/index.js";
export default {
  data: function data() {
    return {
      inp: "",
      password: "",
      isError: false,
      errorCon: ""
    };
  },
  methods: {
    submit: function submit() {
      var _this = this;

      if (!this.inp && !this.password) {
        this.errorCon = "请输入账号和密码";
        this.isError = true;
        return;
      }

      if (!this.inp) {
        this.errorCon = "请输入账号";
        this.isError = true;
        return;
      }

      if (!this.password) {
        this.errorCon = "请输入密码";
        this.isError = true;
        return;
      }

      api.getLoginData({
        params: {
          userName: this.inp,
          password: this.password
        }
      }).then(function (res) {
        console.log(res);

        if (res.data.status == 'success') {
          _this.$store.commit('setUserName', _this.inp);

          console.log(_this.$store.state.userName);

          _this.$router.push('/');
        } else {
          _this.isError = true;
          _this.errorCon = res.data.msg;
        }
      });
    }
  }
};