import api from '@/api/index.js';
import login from './login';
export default {
  data: function data() {
    return {
      nearCity: []
    };
  },
  components: {
    login: login
  },
  created: function created() {
    var _this = this;

    api.getCurPosition().then(function (res) {
      _this.$store.dispatch('setPosition', res.data.data);

      _this.nearCity = res.data.data.nearCity;
    });
  }
};