import api from '@/api/index.js';
import swiper from "@/components/main/swiper.vue";
import mContainer from '@/components/main/container.vue';
export default {
  data: function data() {
    return {
      dataList: [{
        title: '有格调',
        arr: ['全部'],
        class: 'food-bar'
      }, {
        title: '猫眼电影',
        arr: ['正在热映', '即将上映'],
        class: 'movie-bar'
      }, {
        title: '推荐民宿',
        class: 'area-bar',
        arr: ['天津', '济南', '大连', '太原', '石家庄', '秦皇岛', '呼和浩特', '潍坊', '唐山', '泰安']
      }],
      timer: null,
      isDetail: null,
      itemData: []
    };
  },
  created: function created() {
    var _this = this;

    api.getLeftNavList().then(function (res) {
      _this.itemData = res.data.data;
    }); // api.getFirstPageBottomList().then(res => {
    //   this.dataList = res.data.data;
    // })
  },
  methods: {
    enter: function enter(data) {
      clearTimeout(this.timer);
      this.isDetail = data;
    },
    leave: function leave() {
      var _this2 = this;

      this.timer = setTimeout(function () {
        _this2.isDetail = null;
      }, 200);
    },
    isShow: function isShow() {
      clearTimeout(this.timer);
    },
    isLeave: function isLeave() {
      this.isDetail = null;
    },
    regis: function regis() {
      this.$router.push({
        name: 'register'
      });
    },
    login: function login() {
      this.$router.push({
        name: 'login'
      });
    }
  },
  components: {
    swiper: swiper,
    mContainer: mContainer
  }
};